import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { DATE_FORMAT, isConvertedTypeValid, isTransactionTypeValid, UserStatus } from '@core/constants';
import { UserProfileModel } from '@core/models/user-profile.model';
import { ApiService, HttpQuery } from '@core/services/api.service';
import { UsersDateService } from '../../services/users-date.service';
import { faEllipsisV } from '@fortawesome/free-solid-svg-icons';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { DatePickerRangeModel } from '@shared/components/date-picker-range/date-picker-range.model';
import { UsersApi } from '@shared/constants/api';
import { ngbDateToMoment } from '@shared/functions/date.util';
import * as moment from 'moment';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, distinctUntilChanged, map, startWith, switchMap, takeUntil, tap } from 'rxjs/operators';

import { SortableCols, UserModel, UsersByStatusRequest, UsersByStatusResponse } from '../../models/user.model';

@Component({
  selector: 'duelit-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit, OnDestroy {
  displayedColumns: string[] = ['firstName', 'lastName', 'email', 'country', 'status', 'action'];
  dataSource: MatTableDataSource<UserProfileModel> = new MatTableDataSource([]);
  actionIcon = faEllipsisV;
  totalCount = 0;
  dataSource$: Observable<UserModel[]>;
  isLoading = true;
  rangeDate: FormControl;
  date$: BehaviorSubject<DatePickerRangeModel>;
  selectedDateRange: Date;

  page$: BehaviorSubject<number>;
  sortDirection$ = new BehaviorSubject<Sort>({
    active: 'firstName',
    direction: 'desc'
  });
  ngUnsubscribe = new Subject();

  filterFormGroup: FormGroup;

  constructor(
    private api: ApiService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private calendar: NgbCalendar,
    private usersDate: UsersDateService,
  ) { }

  ngOnInit() {
    const initialPage = 1;
    this.page$ = new BehaviorSubject(initialPage);

    this.date$ = new BehaviorSubject(this.usersDate.range);
    this.rangeDate = new FormControl(this.usersDate.range);
    this.rangeDate.valueChanges.pipe(takeUntil(this.ngUnsubscribe)).subscribe((value) => {
      this.usersDate.range = value;
    });
    this.setQueryDate();

    this.filterFormGroup = this.fb.group({
      firstName: new FormControl(),
      lastName: new FormControl(),
      email: new FormControl(),
      country: new FormControl(),
      status: new FormControl(this.getInitialStatus())
    });
    this.updateDate();
    this.dataSource$ = combineLatest([
      this.page$,
      this.sortDirection$,
      this.filterFormGroup.valueChanges.pipe(startWith(this.filterFormGroup.value)),
      this.date$
    ]).pipe(
      distinctUntilChanged(),
      tap(() => this.isLoading = true),
      debounceTime(500),
      switchMap(([page, sortDirection, filter, date]) => {
        const convertedType = this.route.snapshot.queryParams.convertedType;
        const transactionType = this.route.snapshot.queryParams.transaction;
        let path;
        if (isConvertedTypeValid(convertedType)) {
          path = this.api.buildPath(UsersApi.UsersByConverted, [convertedType]);
        } else if (isTransactionTypeValid(transactionType)) {
          path = this.api.buildPath(UsersApi.UsersByTransaction, [transactionType]);
        } else {
          path = this.api.buildPath(UsersApi.UsersByStatus, filter.status ? [filter.status] : ['']);
        }
        const startDate = ngbDateToMoment(date.from).format(DATE_FORMAT);
        const endDate = ngbDateToMoment(date.to).format(DATE_FORMAT);
        const createdStartDate = this.route.snapshot.queryParams.created_start_date;
        const createdEndDate = this.route.snapshot.queryParams.created_end_date;
        const lastActiveStartDate = this.route.snapshot.queryParams.last_active_start_date;
        const lastActiveEndDate = this.route.snapshot.queryParams.last_active_end_date;
        const notStatus = this.route.snapshot.queryParams.not_status;
        const amount = this.route.snapshot.queryParams.amount;
        const query: UsersByStatusRequest = {
          page: page - 1,
          limit: 10,
          start_date: startDate,
          end_date: endDate,
          exclude_fake: true,
          status: filter.status,
          order: sortDirection.active as SortableCols,
          orderState: sortDirection.direction
        };

        if (createdStartDate && createdEndDate) {
          query.created_start_date = createdStartDate;
          query.created_end_date = createdEndDate;
        }

        if (lastActiveStartDate && lastActiveEndDate) {
          query.last_active_start_date = lastActiveStartDate;
          query.last_active_end_date = lastActiveEndDate;
        }

        if (amount) {
          query.amount = amount;
        }
        if (notStatus) {
          query.not_status = notStatus;
        }
        if (filter.firstName) {
          query.first_name = filter.firstName;
        }
        if (filter.lastName) {
          query.last_name = filter.lastName;
        }
        if (filter.email) {
          query.email = filter.email;
        }
        if (filter.country) {
          query.country = filter.country;
        }

        return this.api.get(path, { query: query as unknown as HttpQuery }).pipe(
          catchError(err => {
            return of({
              totalCount: 0,
              results: []
            });
          })
        ) as Observable<UsersByStatusResponse>;
      }),
      tap(results => {
        this.totalCount = results.totalCount;
        this.isLoading = false;
      }),
      map(results => {
        return results.results;
      }),
      takeUntil(this.ngUnsubscribe)
    );

  }

  ngOnDestroy() {
    this.page$.complete();
    this.sortDirection$.complete();
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  private getInitialStatus(): UserStatus | '' {
    const paramStatus = this.route.snapshot.queryParams.status as UserStatus;

    switch (paramStatus) {
      case UserStatus.Active:
      case UserStatus.Deactived:
      case UserStatus.Inactive:
      case UserStatus.Pending:
      case UserStatus.Banned:
        return paramStatus;
      default:
        return '';
    }
  }

  sortData(sort: Sort) {
    if (sort.direction === '') {
      this.sortDirection$.next({
        active: 'firstName',
        direction: 'asc'
      });
    } else {
      this.sortDirection$.next(sort);
    }
  }

  onPageChange(page: number) {
    this.page$.next(page);
  }

  updateDate() {
    this.date$.next({ ...this.rangeDate.value });
  }

  isDateRangeValid() {
    const rangeDate = this.rangeDate.value;

    return rangeDate.from && rangeDate.to;
  }

  private momentToNgbDate(momentDate): NgbDate {
    const calendar = this.calendar.getToday();
    calendar.year = momentDate.year();
    calendar.month = momentDate.month() + 1;
    calendar.day = momentDate.date();

    return calendar;
  }

  private defaultStartDate(): NgbDate {
    const calendar = this.calendar.getToday();

    calendar.year = 2016;
    calendar.month = 1;
    calendar.day = 1;

    return calendar;
  }

  private setQueryDate(): void {
    if (this.route.snapshot.queryParams.start_date && this.route.snapshot.queryParams.end_date) {
      const startDate = moment(this.route.snapshot.queryParams.start_date);
      const endDate = moment(this.route.snapshot.queryParams.end_date);
      this.rangeDate.setValue({
        from: this.momentToNgbDate(startDate),
        to: this.momentToNgbDate(endDate)
      });
    }
  }

  getTotalCount() {
    return this.totalCount === 0 ? this.isLoading ? 'Loading...' : this.totalCount : this.totalCount;
  }

  openUserProfile(user: UserModel) {
    this.router.navigate([`/admin/user`, user.id]);
  }

  openUserStats(user: UserModel) {
    this.router.navigate([`/admin/user`, user.id, 'stats']);
  }

  updateCountry() {}
}


